.App {
  display: flex;
  height: 100vh;
  overflow: hidden;
}

.App > div {
  flex: 1;
  height: auto;
  height: 100%;
}


.App > div:last-child {
  flex: 1;
  height: auto;
  height: 100%;
  overflow: auto;
  padding: 10px;
  padding-top: 0;
}

.ReactCodeMirror > .CodeMirror {
  border: 1px solid #eee;
  height: 100vh;
  background-color: #f6f6f0;
}